.social-icons {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
  }
  
  .social-icons a {
    color: #343a40;
    font-size: 24px;
    transition: color 0.3s;
  }
  
  .social-icons a:hover {
    color: #007bff;
  }
  
  .social-icons a:focus {
    outline: none;
  }
  
  .social-icons a:active {
    transform: translateY(2px);
  }
  
  .social-icons a:last-child {
    margin-right: 0;
  }
  
  p {
    color: #6c757d;
    font-size: 14px;
    margin: 0;
    text-align: center;
  }