.bell-notify {
  position: relative;
}

.bell-icon {
  font-size: 30px;
}

.bell-notify::after {
  content: attr(current-count);
  position: absolute;
  top: 0;
  right: 0;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: red;
  color: white;
  font-size: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1.7px solid white;
}
